<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon class="navbarcolor" id="btnnavbar" @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><a>F.A.Q Alternance</a></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="color" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw;"><v-card width="80%"><v-card-title><a>F.A.Q Alternance</a></v-card-title><v-card elevation="0"><v-card-title> <a>Il est loin de chez nous, Romain…</a></v-card-title><v-card-text><p>Vous avez des missions intéressantes à me proposer ?</p><br><p>On peut trouver un foyer, une chambre, un studio, un igloo ou un canapé à louer à proximité ?</p><p>Une formation informatique en alternance accessible ?</p><p>Des transports publics, du covoiturage, une trottinette à pédale pour les trajets ?</p><br><p>Alors où est le problème :)  ? Et mi-2023, promis, je serai véhiculé !</p></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Une alternance pour quelle formation ?</a></v-card-title><v-card-text><p>J’ai postulé sur la France entière, suis admissible ou en cours d’admission dans de nombreux établissements, et avec une intention d’embauche, les portes des écoles s’ouvrent !</p><p>Formation à Bac+2 ou 3, le choix est vaste !</p><br><p>Voici une liste non exhaustive des formations par département. J’ai des préférences, bien sûr, mais le mieux est d’en discuter, non ?</p><br><li><a href="https://iut.univ-cotedazur.fr/departements-et-formations/informatique/bachelor-universitaire-de-technologie" target="_blank">06 - I.U.T Nice - BUT Informatique </a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">13 - ESGI - Bachelor Architecture des logiciels (bac+3) </a></li><li><a href="https://www.institut-g4.fr/" target="_blank">13 - INSTITUT G4 - Concepteur développeur d'applications (titre professionnel bac+3)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">13 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.eni-ecole.fr/formations/developpement/bac2-developpeur-web-et-web-mobile/" target="_blank">29 - ENI - Développeur web et web mobile (titre bac+2)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">29 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">31 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">33 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.eni-ecole.fr/formations/developpement/bac2-developpeur-web-et-web-mobile/" target="_blank">35 - ENI - Développeur web et web mobile (titre bac+2)</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">38 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">44 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://www.eni-ecole.fr/formations/developpement/bac2-developpeur-web-et-web-mobile/" target="_blank">44 - ENI - Développeur web et web mobile (titre bac+2)</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">51 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">51 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">54 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">59 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">59 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">64 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.purple-campus.com/produit/analyste-developpeur-dapplications-informatiques" target="_blank">66 -  Purple Campus - Analyste développeur d'applications informatiques (Bac +2)</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">69 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">69 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.imie-paris.fr/les-formations/bts-sio-bac-2/" target="_blank">75 - IMIE - BTS SIO (Bac +2) - Admis</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">75 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://www.insta.fr/bts-informatique-sio/" target="_blank">75 - INSTA - BTS SIO</a></li><li><a href="https://imcp.fr/bts-services-informatiques-aux-organisations/" target="_blank">75 - Institut du Marais - BTS SIO</a></li><li><a href="https://www.utec77.fr/bts-services-informatiques-aux-organisations-sio-option-solutions-dinfrastructure-systemes-et-0" target="_blank">75 - UTEC - BTS SIO</a></li><li><a href="https://catalogue.cesi.fr/developpeur-informatique-2169022/" target="_blank">76 - CESI - Développeur informatique (titre bac+2) - En cours d'admission</a></li><li><a href="https://www.eni-ecole.fr/formations/developpement/bac2-developpeur-web-et-web-mobile/" target="_blank">79 - ENI - Développeur web et web mobile (titre bac+2)</a></li><li><a href="https://www.esgi.fr/programmes/architecture-logiciels.html" target="_blank">83 - ESGI - Bachelor Architecture des logiciels (bac+3)</a></li><li><a href="https://www.iscio.net/formations-bts/services-informatiques-aux-organisations/" target="_blank">91 - Institut d'Orsay - BTS SIO</a></li><li><a href="https://www.h3hitema.fr/formation/bts-sio-option-slam/" target="_blank">92 - Hitema - BTS SIO</a></li><li><a href="http://www.ensemblesaintemarie.fr/bts-services-informatiques-aux-organisations/" target="_blank">94 - LGT SAINTE MARIE - BTS SIO</a></li><li><a href="https://www.efrei.fr/programmes-experts/bts-sio/" target="_blank">94 - EFREI - BTS SIO </a></li></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Embaucher Romain, combien ça me coute ?</p></v-card-title><v-card-text><p>Pour un contrat signé <a> <u> avant fin Juin 2022</u></a><a>, à partir de 358 € en moyenne par mois sur 2 ans, charges sociales comprises.</a></p><p>(Salaires en apprentissage – aides exceptionnelles à l’embauche, lissés sur 24 mois)</p><br><p>Pour une présence en entreprise de 50 à 75% du temps selon les écoles.</p><br><p>Un collaborateur à ce prix, ça se tente, non ?</p><br><p><u>Pour en savoir + :</u></p><br><p><a href="https://entreprendre.service-public.fr/vosdroits/F23556">Aides à l’embauche d’un alternant</a></p><br><p> <a href="https://www.service-public.fr/particuliers/actualites/A15108">Rémunération des alternants</a></p><br><p> <a href="https://www.alternance.emploi.gouv.fr/simulateur-employeur/etape-1">Simulateur de cout d'un alternant</a></p><p>(Ma date de naissance : le 22/10/2004)</p><br></v-card-text></v-card><v-card elevation="0"><v-card-title><a>On peut commencer quand ?</a></v-card-title><v-card-text><p>En Juillet/Aout 2022, je suis en immersion Anglais à Malte.</p><br><p>Mais dès début Septembre, je suis libre comme l’air ! Et si ça vous arrange, un contrat peut également débuter plus tard, sur Octobre ou Novembre 2022.</p><br></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Quel est le rythme de l’alternance ?</a></v-card-title><v-card-text><p>Il dépend des formations et des écoles. Le plus souvent, il est de 50 à 75% du temps en entreprise, sur un rythme hebdomadaire.</p><br></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Contrat de professionnalisation ou d’apprentissage, quelle différence ?</a></v-card-title><v-card-text><p>En termes d’organisation de la formation en alternance, rien !</p><br><p>En termes de rémunération, de statut,… quelques différences existent, surtout pour l’employeur.</p><br><p>La plupart des établissements d’alternance proposent les 2 formules.</p><br><p> <a href="https://www.service-public.fr/particuliers/vosdroits/F31704">Pour en savoir plus</a></p><br></v-card-text></v-card><v-card elevation="0"><v-card-title><a>Embaucher en alternance, c’est compliqué ?</a></v-card-title><v-card-text><a>Et non, c’est tout simple : Un formulaire d’embauche, une convention avec l’école, et un contact avec votre Opco. Vous êtes accompagné, vos interlocuteurs s’occupent de presque tout !</a><br></v-card-text></v-card><v-card elevation="0"><v-card-title><a>J'ai encore des questions ?</a></v-card-title><v-card-text><a>Consultez ma page contact ! Si je n'ai pas la réponse actuellement, je la trouverai !</a></v-card-text></v-card></v-card></div></div></v-layout></div>